off') format('woff');\r\n   font-display: swap;\r\n   font-weight: 700;\r\n   font-style: normal;\r\n}\r\n\r\n@font-face {\r\n   font-family: 'TTHoves-Medium';\r\n   src: url('../assets/fonts/TTHoves-Medium.woff2') format('woff2'),\r\n      url('../assets/fonts/TTHoves-Medium.woff') format('woff');\r\n   font-display: swap;\r\n   font-weight: 500;\r\n   font-style: normal;\r\n}\r\n\r\n@font-face {\r\n   font-family: 'TTHoves-Regular';\r\n   src: url('../assets/fonts/TTHoves-Regular.woff2') format('woff2'),\r\n      url('../assets/fonts/TTHoves-Regular.woff') format('woff');\r\n   font-display: swap;\r\n   font-weight: 400;\r\n   font-style: normal;\r\n}\r\n\r\n@font-face {\r\n   font-family: 'TTHoves-Light';\r\n   src: url('../assets/fonts/TTHoves-Light.woff2') format('woff2'),\r\n      url('../assets/fonts/TTHoves-Light.woff') format('woff');\r\n   font-display: swap;\r\n   font-weight: 300;\r\n   font-style: normal;\r\n}\r\n\r\n@font-face {\r\n   font-family: 'SFProDisplay-Black';\r\n   src: url('../assets/fonts/SFProDisplay-Black.woff2') format('woff2'),\r\n      url('../assets/fonts/SFProDisplay-Black.woff') format('woff');\r\n   font-display: swap;\r\n   font-weight: 900;\r\n   font-style: normal;\r\n}","@import url('./_fonts.scss');\r\n\r\n:root {\r\n   /* colors */\r\n   --clr-white: 0 0% 95%;\r\n   --clr-light: 0 0% 90%;\r\n   --clr-grey: 0 0% 74%;\r\n   --clr-dark: 0 0% 20%;\r\n\r\n   /* font-sizes */\r\n   --fs-200: 12.5rem;\r\n   --fs-64: 4rem;\r\n   --fs-40: 2.5rem;\r\n   --fs-22: 1.375rem;\r\n   --fs-18: 1.125rem;\r\n   --fs-16: 1rem;\r\n   --fs-14: .875rem;\r\n   --fs-12: .75rem;\r\n\r\n   /* font-families */\r\n   --ff-black: 'SFProDisplay-Black', sans-serif;\r\n   --ff-bold: 'TTHoves-Bold', sans-serif;\r\n   --ff-medium: 'TTHoves-Medium', sans-serif;\r\n   --ff-regular: 'TTHoves-Regular', sans-serif;\r\n   --ff-light: 'TTHoves-Light', sans-serif;\r\n\r\n   /* heights */\r\n   --header-height: 6rem;\r\n}",".flex {\r\n   display: flex;\r\n   gap: var(-