e;\r\n\r\n   span {\r\n      font-family: var(--ff-light);\r\n      display: block;\r\n      color: hsl(var(--clr-grey) / var(--opacity, 1));\r\n   }\r\n}\r\n\r\n.main-button {\r\n   width: 13.8rem;\r\n   height: 4.3rem;\r\n   display: flex;\r\n   align-items: center;\r\n   justify-content: center;\r\n   gap: .75rem;\r\n   border: none;\r\n   cursor: pointer;\r\n   user-select: none;\r\n   text-decoration: none;\r\n\r\n   span {\r\n      position: relative;\r\n      display: inline-block;\r\n      width: 1rem;\r\n      height: 1px;\r\n      border-radius: 1rem;\r\n      background-color: var(--arrow-color, hsl(var(--clr-dark)));\r\n      transition: .1s all ease-in-out;\r\n\r\n      &::after,\r\n      &::before {\r\n         content: \"\";\r\n         position: absolute;\r\n         right: -2px;\r\n         width: .5rem;\r\n         height: 1px;\r\n         border-radius: 1rem;\r\n         background-color: var(--arrow-color, hsl(var(--clr-dark)));\r\n      }\r\n\r\n      &::after {\r\n         top: 2.6px;\r\n         transform: rotate(-45deg);\r\n      }\r\n\r\n      &::before {\r\n         bottom: 2.6px;\r\n         transform: rotate(45deg);\r\n      }\r\n   }\r\n\r\n   &:hover span {\r\n      transform: translateX(.5rem);\r\n   }\r\n\r\n   &:active span {\r\n      transform: translateX(1rem);\r\n   }\r\n}\r\n\r\n.slider-button {\r\n   position: relative;\r\n   width: 3.3rem;\r\n   height: 3.3rem;\r\n   display: flex;\r\n   align-items: center;\r\n   justify-content: center;\r\n   border: 1px solid hsl(var(--clr-grey) / var(--opacity, 1));\r\n   cursor: pointer;\r\n   transition: .15s all ease-in-out;\r\n\r\n   &:hover {\r\n      background-color: hsl(var(--clr-grey) / var(--opacity, .2));\r\n   }\r\n\r\n   span {\r\n      position: relative;\r\n      display: inline-block;\r\n      width: 1rem;\r\n      height: .0625rem;\r\n      border-radius: 1rem;\r\n      background-color: var(--arrow-color, hsl(var(--clr-dark)));\r\n      transition: .2s all ease-in-out;\r\n